import { PropsWithChildren, memo, forwardRef } from 'react';
import cn from 'classnames';
import { TypographyCommonProps } from '@thyme-ui/types';

import { getHTMLTag } from './utils/getHTMLTag';

/**
 * any ref's type for now.
 * Error with resolving ref-types (inside or outside of component)
 * Type with intersections (HTMLDivElement & HTMLSpanElement)) works in component, but move error to outside useRef<Type>
 */

export const Typography = memo(
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  forwardRef<any, PropsWithChildren<TypographyCommonProps>>(
    (
      {
        children,
        type = 'paragraph',
        weight = 'normal',
        size = 'medium',
        tag,
        color,
        className = '',
        onClick,
        tabIndex,
        title = '',
        withTruncate = false
      }: PropsWithChildren<TypographyCommonProps>,
      ref
    ) => {
      const Tag = tag || getHTMLTag(type, size);

      const colorValue = !!color && !color?.includes('#') ? `var(--${color})` : color;

      const styles = !!colorValue ? { color: colorValue } : undefined;

      const allClasses = cn(
        'thuiTypography',
        `thuiTypography--${type}`,
        `thuiTypography--${weight}Weight`,
        `thuiTypography--${size}`,
        className,
        { 'thuiTypography--truncate': withTruncate }
      );

      return (
        <Tag className={allClasses} style={styles} onClick={onClick} tabIndex={tabIndex} title={title} ref={ref}>
          {children}
        </Tag>
      );
    }
  )
);
