import { DefaultTagType, TYPOGRAPHY_HEADER_SIZE, TypographySizes, TypographyTypes } from '@thyme-ui/types';

export const getHTMLTag = (type: TypographyTypes, size: TypographySizes): DefaultTagType => {
  switch (type) {
    case 'heading':
      if (size in TYPOGRAPHY_HEADER_SIZE) {
        return `h${size}` as DefaultTagType;
      }
      return 'h1';
    case 'paragraph':
      return 'p';
    case 'compact':
      return 'span';
    default:
      return 'p';
  }
};
