import type { ColorNames } from './Colors';

export const TYPOGRAPHY_HEADER_SIZE = ['1', '2', '3', '4', '5', '6'] as const;

export type TypographyHeaderSize = (typeof TYPOGRAPHY_HEADER_SIZE)[number];

export type TypographySizes = 'xLarge' | 'large' | 'medium' | 'small' | 'xSmall' | TypographyHeaderSize;

export type TypographyWight =
  | 'thin'
  | 'extraLight'
  | 'light'
  | 'normal'
  | 'medium'
  | 'semiBold'
  | 'bold'
  | 'extraBold'
  | 'black';

export type DefaultTagType = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p' | 'span';

export type TypographyTypes = 'heading' | 'paragraph' | 'compact';

export type TypographyTags = 'div' | 'span' | 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p' | 'label' | 'option';

export interface TypographyCommonProps {
  /** Type name */
  type?: TypographyTypes;
  /** Size name */
  size?: TypographySizes;
  /** Wight name */
  weight?: TypographyWight;
  /** Custom tag */
  tag?: TypographyTags;
  /** Additional class names */
  className?: string;
  /** Color name */
  color?: ColorNames;
  /** Click event handler */
  onClick?: () => void;
  /** Tabindex */
  tabIndex?: number;
  /** Title attribute */
  title?: string;
  /** Cut text if it overflow one line */
  withTruncate?: boolean;
}
